<template>
  <div class="signIn subPage">
    <header>
      <h2>{{ signIn.title }}</h2>
      <p>{{ signIn.headerSub }}</p>
      <!-- <small>Development organization</small> -->
    </header>
    <section class="wrapper style5">
      <div class="inner">
        <h2 class="text-danger">{{ signIn.alertTxt }}</h2>
        <p v-for="(el, i) in signIn.descAry" :key="i" :class="el.class">
          {{ el.contTxt }}
        </p>
        <h3 class="align-center">{{ signIn.subTitle }}</h3>
        <article class="formBox">
          <h5>加入會員表單</h5>
          <ValidationObserver ref="observer" tag="form" @submit.prevent="submitHandler" >
            <section class="row gtr-uniform">
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|min:3" v-slot="{ errors }">
                  <input v-model="formData.realname" type="text" name="realname" id="realname" placeholder="會員姓名" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|min:2" v-slot="{ errors }">
                  <input v-model="formData.siteurl" type="text" :maxlength="10" name="siteurl" id="siteurl" placeholder="專屬網址 二到十位數(英數)" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|min:10" v-slot="{ errors }">
                  <input v-model="formData.phone" type="text" :maxlength="10" name="phone" id="phone" placeholder="聯絡電話" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|email" v-slot="{ errors }">
                  <input v-model="formData.email" type="email" name="email" id="email" placeholder="電子郵件" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|min:10" v-slot="{ errors }">
                  <input v-model="formData.account" type="text" name="account" id="account" placeholder="會員帳號 (登入會員專區用)" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|min:6" vid="confirmation" v-slot="{ errors }">
                  <input v-model="formData.pw" type="password" name="passwd" id="pw" placeholder="會員密碼 最少六位數 (登入會員專區用)" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6 col-12-small">
                <ValidationProvider rules="required|confirmed:confirmation" v-slot="{ errors }">
                  <input v-model="formData.confirmed" type="password" name="confirmed" id="confirmed" placeholder="確認密碼" />
                  <small class="text-danger">{{ errors[0] }}</small>
                </ValidationProvider>
              </div>
              <div class="col-6">
                <VerifyModules ref="verifyModules"
                  :verifyCode="verifyCode"
                  @updateVerifyCode="updateVerifyCode"/>
              </div>
              <div class="col-12">
                <span v-html="signIn.note" />
              </div>
              <div class="col-12">
                <ul class="actions">
                  <li><input @click="submitHandler" type="submit" value="送出申請" class="primary" /></li>
                  <li><input @click="restHandler" type="reset" value="清除" /></li>
                </ul>
              </div>
            </section>
          </ValidationObserver>
        </article>
      </div>
    </section>
  </div>
</template>
<script>
import { ValidationObserver, ValidationProvider, extend, localize } from 'vee-validate'
import { required, email, confirmed } from 'vee-validate/dist/rules'
import TW from 'vee-validate/dist/locale/zh_TW.json'
import VerifyModules from './verifyModules'

localize('zh_TW', TW)
extend('email', email)
extend('confirmed', confirmed)
extend('required', {
  ...required,
  message: '必填欄位！'
})
extend('min', {
  validate (val, args) {
    return val.length >= args.length
  },
  params: ['length'],
  message: '長度不夠！'
})

export default {
  name: 'SignIn',
  components: {
    ValidationObserver,
    ValidationProvider,
    VerifyModules
  },
  data () {
    return {
      firstName: '',
      lastName: '',
      email: '',
      signIn: this.$store.state.pageData.signIn,
      formData: {
        realname: '',
        siteurl: '',
        phone: '',
        email: '',
        account: '',
        pw: '',
        confirmed: '',
        verify: ''
      },
      verifyCode: null
    }
  },
  methods: {
    // 驗證碼更新 Code
    updateVerifyCode (val) {
      this.verifyCode = val
    },
    // 清除
    restHandler () {
      this.verifyCode = null
      this.$refs.observer.reset()
      this.$refs.verifyModules.$refs.Verify.refresh()
      this.formData = this.$options.data.call(this).formData
    },
    // 檢查驗證碼
    checkVerify () {
      this.$refs.verifyModules.$refs.Verify.checkCode()
    },
    // 表單驗證
    async submitHandler () {
      this.checkVerify()
      const isValid = await this.$refs.observer.validate()
      if (isValid && this.verifyCode) {
        this.restHandler()
        // 建立 formData 物件
        const fd = new FormData(document.querySelector('form'))
        // fetch post
        fetch('member/pages2_post1.php', {
          method: 'POST',
          body: fd,
          headers: { 'Content-Type': 'multipart/form-data' }
        }).then((res) => res.json()).then(() => {
          alert('送出成功！')
        })
      }
    }
  }
}
</script>

<style lang="scss">
.signIn {
  padding-top: 60px;
  header {
    height: 12vh;
    padding: 1rem 2rem;
  }
  .formBox {
    border: 2px solid rgba(0,0,0,.08);
    border-radius: 5px;
    padding: 1rem 2rem;
    background-color: white;
    .gtr-uniform {
      small {
        font-size: smaller;
      }
    }
  }
}
</style>
